<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <button @click="sendAjax">get请求send</button>
    <button @click="sendPost">post请求send</button>
    <button @click="concurrentSend">并发请求</button>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to YUYU Vue.js App'
    }
  },
  methods:{
    sendAjax(){
      this.$axios.get('/api/test.json',{
        params: {
          ...postData,
        }
      })
        .then(
          res=>{
            console.log(res.data)
          })
        .catch(err=>{
          console.log(err)
        })
    },
    sendPost(){
      this.$axios.post('/api/test.json',{"a":1})
        .then(
          res=>{
            console.log(res.data)
          })
        .catch(err=>{
          console.log(err)
        })
    },
    concurrentSend(){
//      this.$axios.defaults.baseURL='http://localhost:8081/';
      let post=this.$axios.post('api/test.json',{"a":1});

       let get=this.$axios.get('api/test.json');

      this.$axios.all([post, get])
        .then(this.$axios.spread(function (acct, perms) {
        //两个请求现已完成
        console.info(acct)
      }));
    }
  }
}

let postData = {
  companyCode:'tur',
  userName:'123456789123456789',
  password:'123456'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



