<template>
	<div>
		<el-form ref="form" :model="form" label-width="80px;" class="login-box" :rules="rules">
			<h3>欢迎登录yumi商城后台</h3>
			<el-form-item label="用户名" prop="username">
				<el-input type="text" placeholder="请输入用户名" v-model="form.username"></el-input>
			</el-form-item>
			<el-form-item label="密码" prop="password">
				<el-input type="password" placeholder="请输入密码" v-model="form.password"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="clickSubmit('form')">登录</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		name : "Login",
		data() {
			return{
				form: {
					username:"",
					password:""
				},
				rules:{
					username:[
						{required:true,message:'请输入用户名',trigger:'blur'}
					],
					password:[
						{required:true,message:'请输入密码',trigger:'blur'}
					]
				}
			}
		},
		methods:{
			clickSubmit(formName){
				this.$refs[formName].validate(valid=>{
					if(valid){
						this.$router.push("/home")
					}else{
					    this.$message.error('用户名和密码必须填写');
						console.log("error")
						return false;
					}
				})
			}
		}
	}

</script>

<style>
	.login-box{
		width: 350px;
		margin: 180px auto;
		border: 1px solid #DCDFE6;
		padding: 35px;
		border-radius: 5px;
		box-shadow: 0 0 15px;
	}

</style>



